}


@-webkit-keyframes fadein {
    from { width: 0px; overflow: hidden; }
    to   { width: 300px; }
}


@keyframes fadein {
    from { width: 0px; overflow: hidden; }
    to   { width: 300px; }
}

.sidebar {
  width: 300px;
  height: 100%;
  position: fixed;
  background-color: #f4f4f4;
  font-family: 'Work Sans';
  -webkit-animation: fadein 0.4s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
