  margin-top: 30px;
}

.links a {
  letter-spacing: 0.5px;
  margin-bottom: 7px;
  color: grey;
  text-decoration: none;
  display: inline-block;
  color: grey;
  padding-bottom: .25em;
  text-decoration: none;
  background: linear-gradient(to bottom, #1bad92 0%, #1bad92 100%);
  background: linear-gradient(to bottom, var(--main-color) 0%, var(--main-color) 100%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 0px 0px;
  text-decoration: none;
}

.links a:hover {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg id='squiggle-link' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:ev='http://www.w3.org/2001/xml-events' viewBox='0 0 20 4'%3E%3Cstyle type='text/css'%3E.squiggle{animation:shift .3s linear infinite;}@keyframes shift {from {transform:translateX(0);}to {transform:translateX(-20px);}}%3C/style%3E%3Cpath fill='none' stroke='%231bad92' stroke-width='2' class='squiggle' d='M0,3.5 c 5,0,5,-3,10,-3 s 5,3,10,3 c 5,0,5,-3,10,-3 s 5,3,10,3'/%3E%3C/svg%3E");
  background-position: 0 100%;
  background-size: auto 3px;
  background-repeat: repeat-x;
  text-decoration: none;
  color: black;
}


.icons a {
  color: grey;
  margin-right: 15px;
}

.icons a:hover {
  color: black;
}

.wrapper {
  padding: 10px;
  margin: 0 auto;
  width: 500px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .item {
    visibility: hidden;
    display: none;
  }

  h2 {
    font-size: 50px;
    margin: 0;
    color: #3a3a3a;
    text-align: left;
  }

  > div {
    min-height: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: flex;
    align-items: flex-end;
    font-weight: bold;
    color: orange;

    &:after {
content: "";
    width: 10px;
    height: 4px;
    display: block;
    background: black;
    color: orange;
    margin-bottom: 4px;
    margin-left: 2px;
    -webkit-animation-duration: 350ms;
            animation-duration: 350ms;
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    }
  }
}

@-webkit-keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}


